<template>
  <div class="transactionHistory">
    <loading :active.sync="isLoading"
             :is-full-page="false"
             color="#0060f1"
             loader="dots">
    </loading>
    

    
    <div class="block__row lineRow">
      <rangedate-picker class="daterange lineRow__row" @selected="selectRange"></rangedate-picker>
  
      <ul class="switcher">
        <li  v-for="(item, i) in switchers"  :key="i"
             @click="() => {item.action(); currentSwitcher = item.switcherName}"
             :class="['switcher__item', {'switcher__item-active' : item.switcherName === currentSwitcher}]">
          {{item.title}}
        </li>
      </ul>
    </div>
    
    <div class="transaction-history block" >
      <table class="transactionTable">
        <thead class="transactionTable__thead">
          <tr class="">
            <td class="">Transaction ID</td>
            <td class="">Date</td>
            <td class="">Method</td>
            <td class="">Status</td>
            <td class="">Amount ({{currency}})</td>
          </tr>
        </thead>
        <tbody  class="transactionTable__tbody" v-if="currentSwitcher === 'deposit'">
          <tr class="" v-for="item in orderedHistory" :key="item.id">
              <td class="">{{ item.transactionID }}</td>
              <td class="">
                {{formattedDate(item.time)}}
              </td>
              <td class=""><span :class="['transactionTable__method', 'bg-card', `bg-card__${item.debit.name}`]"></span></td>
              <td class="transactionTable__status" :style="{color: '#86C339'}">Success</td>
            <td class="transactionTable__amount">{{ item.credit.amount }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    
    <paginate
      v-if="totalPageCount > 1"
      v-model="requestParams.pageIndex"
      :page-count="totalPageCount"
      :page-range="3"
      :margin-pages="2"
      :click-handler="pageChanged"
      :prev-text="'<'"
      :next-text="'>'"
      :container-class="'pagination'"
      :page-class="'page-item'">
    </paginate>
  </div>

</template>

<script>
	import Paginate from 'vuejs-paginate';
  import VueRangedate from 'vue-rangedate-mm';
  import call from "@/services/callService";
  import {mapGetters} from 'vuex';
  
  export default {
		name: "TransactionHistory",
    components: {
      Paginate,
      'rangedate-picker': VueRangedate,
    },
		data() {
			return {
        isLoading: false,
        requestParams: {
          type : 'Deposit',
          startTime: '1970-04-04T16:00:00.000Z',
          endTime: new Date(),
          pageIndex: 1,
          pageSize: 8
        },
        switchers: [
          { title: 'Deposit history', action: this.showDeposit, switcherName: 'deposit' },
          { title: 'Withdraw history', action: this.showWithdraw, switcherName: 'withdraw'  }
        ],
        currentSwitcher: 'deposit',
        sortTypes: ['Latest', 'Oldest'],
        currentSortType: 'Latest',
        totalPageCount: 0,
        history: [],
			}
		},
    created() {
      this.getTransactionHistory(this.requestParams);
    },
		methods: {
      async showDeposit() {
        if ( this.requestParams.type !== 'Deposit') {
          this.requestParams.type = 'Deposit';
          this.getTransactionHistory(this.requestParams);
        }
      },
      showWithdraw() {
        if ( this.requestParams.type !== 'Withdraw') {
          this.requestParams.type = 'Withdraw';
          this.getTransactionHistory(this.requestParams);
        }
      },
      selectRange(range) {
        if (!range.start || !range.end) {
          this.pageIndex = 1;
        }
        this.requestParams.startTime = range.start ? range.start : '1970-04-04T16:00:00.000Z';
        this.requestParams.endTime   = range.end   ? new Date(range.end.getTime() + (23 * 60* 60000 + (59 * 60000) + (59999))) : new Date();
        
        this.getTransactionHistory(this.requestParams);
      },
      pageChanged() {
        this.getTransactionHistory(this.requestParams);
      },
      async getTransactionHistory(params) {
        this.isLoading = true;
        try {
          const result = await call.getTransactionHistory(params);
          this.totalPageCount = result.kwargs.totalPageCount;
         
          this.requestParams.pageIndex = result.kwargs.currentPageIndex ? result.kwargs.currentPageIndex : 1;
          this.history = result.kwargs.transactions;
  
          this.$notify({
            group: 'app',
            type:'success',
            text: 'History updated'
          });
        } catch(err) {
          const message = err.kwargs.desc || JSON.stringify(err);
          console.error(`EM Getting transaction history error: ${message}`);
  
          this.$notify({
            group: 'app',
            type:'error',
            text: message
          });
        } finally {
          this.isLoading = false;
        }
      },
      sortHistory(sort_by) {
        this.currentSortType = sort_by;
      },
      colorStatus(status) {
        switch(status) {
          case 'Done'    : return '#86C339';
          case 'Pending' : return '#FFC000';
          case 'anceled': return '#F13F35';
          default        : return '#191919';
        }
      },
      formattedDate(date) {
        let d = new Date(date);
        return `${d.getDate()}.${d.getMonth() + 1}.${d.getFullYear()}`
      }
		},
    computed: {
      ...mapGetters({
        currency: 'getUserCurrency'
      }),
      orderedHistory() {
        if (this.currentSortType === 'Oldest') {
          return [...this.history].reverse();
        }
        return this.history;
      }
    },
		
	}
</script>

<style lang="sass" scoped>
  .transactionHistory
    position: relative
  .switherRow
    position: relative
    margin-top: 0
  /deep/.calendar-buttons
    display: flex
  .lineRow
    display: flex
    &__row
      flex-grow: 1
      margin-right: rem(20)
      &:last-child
        margin-right: 0
        
  .sorting
    max-width: rem(200)
  .daterange
    max-width: rem(300)

  .transaction-history
    display: block
    margin-top: 0
    padding: 0
    
  .transactionTable
    width: 100%
    text-align: center
    border-collapse: collapse
    td
      padding: rem(15)
    &__thead
      background: rgba(243, 244, 245, 0.2)
      td
        border: rem(1) solid #F3F4F5
    &__tbody
      td
        border-bottom: rem(1) solid #F3F4F5
      tr:hover
        td
          background: rgba(243, 244, 245, 0.3)
    &__amount
      font-weight: 500
      text-align: right
    &__status
      font-weight: 500
      text-align: left
    &__method
      margin: rem(-15) 0
      width: rem(60)
      height: rem(35)
      display: inline-flex
      
    .bg-card
      background-repeat: no-repeat
      background-size: contain
      &__BankTransfer
        background-image: url('../../../assets/img/banks/bank_transfer.png')
      &__CashToCode
        background-image: url('../../../assets/img/banks/bank_cashtocode.png')
      &__CreditCard
        background-image: url('../../../assets/img/banks/bank_creditcatd.png')
      &__CryptoPay
        background-image: url('../../../assets/img/banks/bank_crypto.png')
      &__EcoPayz
        background-image: url('../../../assets/img/banks/bank_ecopayz.png')
      &__Sofort
        background-image: url('../../../assets/img/banks/bank_sofor.png')
      &__WebMoney
        background-image: url('../../../assets/img/banks/bank_webmoney.png')
      &__Go
        background-image: url('../../../assets/img/banks/bank_jetongo.png')
      &__MuchBetter
        background-image: url('../../../assets/img/banks/bank_muchbetter.png')
      &__Neteller
        background-image: url('../../../assets/img/banks/bank_neteller.png')
      &__PaySafeCard
        background-image: url('../../../assets/img/banks/bank_paysafecard.png')
      &__SafetyPay
        background-image: url('../../../assets/img/banks/bank_safetypay.png')
      &__Sofort
        background-image: url('../../../assets/img/banks/bank_sofor.png')
      &__Skrill
        background-image: url('../../../assets/img/banks/bank_sckrill.png')
      &__Trustly
        background-image: url('../../../assets/img/banks/bank_trusty.png')
      &__PayNPlay
        background-image: url('../../../assets/img/banks/bank_play_n_play.png')
      &__Zimpler
        background-image: url('../../../assets/img/banks/bank_zimpler.png')
</style>
